
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import Uploader from "vux-uploader";
import Exif from "exif-js";
export default {
name: "myIdCard",
components: {
ViewBox,
XHeader,
Uploader,
Divider,
XButton
},
mounted () {
this.queryUser();
this.getQnToken();
this.queryUserIdCard();
},
data() {
return {
idCardObj: {},
userInfo: {},
value: "",
headerImage: "",
headerImage2: "",
picValue: "",
picValue2: "",
qnToken: '',
imgKey: '',
imgKey2: ''
};
},
methods: {
savePic() {
this.$api
.alterUserIdCard({
frontPhoto: this.imgKey,
reversePhoto: this.imgKey2
})
.then(data => {
if (data.code === 200) {
this.$vux.toast.text('上传成功', "top");
this.$router.go(-1);
} else {
this.$vux.toast.text(data.message, "top");
}
});
},
getQnToken() {
this.$api
.getQnToken({})
.then(data => {
if (data.code === 200) {
this.qnToken = data.data.token;
} else {
this.$vux.toast.text(data.message, "top");
}
});
},
queryUserIdCard() {
this.$api
.queryUserIdCard({})
.then(data => {
if (data.code === 200) {
if (data.data.idCard) {
this.headerImage = data.data.idCard.frontPhoto;
this.headerImage2 = data.data.idCard.reversePhoto;
}
} else {
this.$vux.toast.text(data.message, "top");
}
});
},
queryUser() {
this.$api
.queryUser({})
.then(data => {
if (data.code === 200) {
this.userInfo = data.data.user;
}
});
},
upload(e) {
let files = e.target.files || e.dataTransfer.files;
if (!files.length) return;
this.picValue = files[0];
this.imgPreview(this.picValue, 1);
},
upload2(e) {
let files = e.target.files || e.dataTransfer.files;
if (!files.length) return;
this.picValue2 = files[0];
this.imgPreview(this.picValue2, 2);
},
imgPreview(file, index) {
let self = this;
let Orientation;

Exif.getData(file, function() {
Orientation = Exif.getTag(this, "Orientation");
});

if (!file || !window.FileReader) return;

if (/^image/.test(file.type)) {

let reader = new FileReader();

reader.readAsDataURL(file);

reader.onloadend = function() {
let result = this.result;
let img = new Image();
img.src = result;
console.log(img);


if (this.result.length <= 100 * 1024) {
if (index === 1) {
self.headerImage = this.result;
} else if (index === 2) {
self.headerImage2 = this.result;
}
self.postImg(this.result, index);
} else {
img.onload = function() {
let data = self.compress(img, Orientation);

if (index === 1) {
self.headerImage = data;
} else if (index === 2) {
self.headerImage2 = data;
}
self.postImg(data, index);
};
}
};
}
},
postImg(data, index) {
let self = this;
let pic = data.substring(23);
let url = "http://upload.qiniup.com/putb64/-1" 
let xhr = new XMLHttpRequest();
xhr.onreadystatechange=function(){
if (xhr.readyState==4){
if (index === 1) {
self.imgKey = JSON.parse(xhr.responseText).key;
} else if (index === 2) {
self.imgKey2 = JSON.parse(xhr.responseText).key;
}
console.log(self.imgKey2);

}
}
xhr.open("POST", url, true);
xhr.setRequestHeader("Content-Type", "application/octet-stream");
xhr.setRequestHeader("Authorization", "UpToken " + this.qnToken);
xhr.send(pic);
},
rotateImg(img, direction, canvas) {

const min_step = 0;
const max_step = 3;
if (img == null) return;

let height = img.height;
let width = img.width;
let step = 2;
if (step == null) {
step = min_step;
}
if (direction == "right") {
step++;

step > max_step && (step = min_step);
} else {
step--;
step < min_step && (step = max_step);
}

let degree = step * 90 * Math.PI / 180;
let ctx = canvas.getContext("2d");
switch (step) {
case 0:
canvas.width = width;
canvas.height = height;
ctx.drawImage(img, 0, 0);
break;
case 1:
canvas.width = height;
canvas.height = width;
ctx.rotate(degree);
ctx.drawImage(img, 0, -height);
break;
case 2:
canvas.width = width;
canvas.height = height;
ctx.rotate(degree);
ctx.drawImage(img, -width, -height);
break;
case 3:
canvas.width = height;
canvas.height = width;
ctx.rotate(degree);
ctx.drawImage(img, -width, 0);
break;
}
},
compress(img, Orientation) {
let canvas = document.createElement("canvas");
let ctx = canvas.getContext("2d");

let tCanvas = document.createElement("canvas");
let tctx = tCanvas.getContext("2d");
let initSize = img.src.length;
let width = img.width;
let height = img.height;
console.log(width);


let ratio;
if ((ratio = width * height / 4000000) > 1) {
console.log("大于400万像素");
ratio = Math.sqrt(ratio);
width /= ratio;
height /= ratio;
} else {
ratio = 1;
}
canvas.width = width;
canvas.height = height;

ctx.fillStyle = "#fff";
ctx.fillRect(0, 0, canvas.width, canvas.height);

let count;
if ((count = width * height / 1000000) > 1) {
console.log("超过100W像素");
count = ~~(Math.sqrt(count) + 1); 

let nw = ~~(width / count);
let nh = ~~(height / count);
tCanvas.width = nw;
tCanvas.height = nh;
for (let i = 0; i < count; i++) {
for (let j = 0; j < count; j++) {
tctx.drawImage(
img,
i * nw * ratio,
j * nh * ratio,
nw * ratio,
nh * ratio,
0,
0,
nw,
nh
);
ctx.drawImage(tCanvas, i * nw, j * nh, nw, nh);
}
}
} else {
ctx.drawImage(img, 0, 0, width, height);
}

if (Orientation != "" && Orientation != 1) {
switch (Orientation) {
case 6: //需要顺时针（向左）90度旋转
this.rotateImg(img, "left", canvas);
break;
case 8: //需要逆时针（向右）90度旋转
this.rotateImg(img, "right", canvas);
break;
case 3: //需要180度旋转
this.rotateImg(img, "right", canvas); 
this.rotateImg(img, "right", canvas);
break;
}
}

let ndata = canvas.toDataURL("image/jpeg", 0.1);
console.log("压缩前：" + initSize);
console.log("压缩后：" + ndata.length);
console.log(
"压缩率：" + ~~(100 * (initSize - ndata.length) / initSize) + "%"
);
tCanvas.width = tCanvas.height = canvas.width = canvas.height = 0;
console.log(ndata);

return ndata;
}
}
};



